import React from 'react';
import { useSelector } from 'react-redux';
import { getListBannerSelector } from 'src/redux/selectors/common.selector';
import PromotionDetailView from './promotionDetail.view';

const PromotionDetailContainer = (props) => {
  const promotion = props?.location?.state?.item;
  const listBanner = useSelector(getListBannerSelector);
  return <PromotionDetailView listBanner={listBanner} promotion={promotion} />;
};

export default PromotionDetailContainer;
